.card-container {
  width: 300px;
  overflow: hidden;
  box-shadow: 0px 0px 15px -5px;
  transition: 0.5s;
  animation: ease-in-out;
  border-radius: 5px;
}

.card-container:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 15px 0px;
}

.image-container img {
  overflow: hidden;
  height: 200px;
  width: 100%;
}

.card-content {
  margin: 1rem;
  margin-top: 0.5rem;
}

h3,
p {
  /* Resetting to default value */
  margin: 0;
  padding: 0;
}

.card-title {
  margin-bottom: 0.5rem;
}
